@font-face {
    font-family: "Roboto", sans-serif;
    src: url("../../../example/src/fonts/Roboto/Roboto-Bold.ttf");
    font-weight: bold;
    font-style: normal;
}
@font-face {
    font-family: "Roboto", sans-serif;
    src: url("../../../example/src/fonts/Roboto/Roboto-Medium.ttf");
    font-weight: 500;
    font-style: normal;
}
@font-face {
    font-family: "Roboto", sans-serif;
    src: url("../../../example/src/fonts/Roboto/Roboto-Regular.ttf");
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: "Roboto", sans-serif;
    src: url("../../../example/src/fonts/Roboto/Roboto-Light.ttf");
    font-weight: 300;
    font-style: normal;
}
* {
    font-family: "Roboto", sans-serif;
}
.wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.green {
    color: #52c41a !important;
}
.red {
    color: #ff4d4f !important;
}
.link {
    text-decoration: none;
    text-transform: uppercase;
}
.link:active,
.link:focus,
.link:visited,
.link:hover {
    text-decoration: none;
}
.header {
    padding: 12px 28px 12px 18px;
    background: #fff;
    -webkit-box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.05);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}
.header__circle {
    width: 8px;
    height: 8px;
    min-width: 8px;
    min-height: 8px;
    border-radius: 50%;
    margin-right: 4px;
}
.header__circle_green {
    background: #52c41a;
}
.header__circle_red {
    background: #52c41a;
}
.header__info {
    margin-left: auto;
}
.header__info_text {
    font-size: 14px;
    line-height: 22px;
}
.header__info_date {
    font-size: 12px;
    line-height: 130%;
    color: #828282;
    margin-top: 1px;
}
.header__menu {
    margin-left: 87px;
}
.header__menu_punct {
    margin-right: 24px;
    font-size: 14px;
    line-height: 22px;
    color: #333333;
}
.header__menu_punct:active,
.header__menu_punct:focus,
.header__menu_punct:hover {
    color: #2f80ed;; 
}
.header__menu_punct:last-child {
    margin-right: 0;
}
.header__menu_punct-active {
    font-weight: 600;
    color: #2f80ed;
}
.header__menu_punct-active:hover {
    color: #2f80ed;
}
.header__loupe {
    margin-left: 25px;
}
.header__lang {
    margin-left: 26px;
}
.header__lang_chosen {
    margin-right: 5px;
}
.header__wallet {
    margin-left: auto;
    cursor: pointer;
}
.header__wallet_text {
    font-size: 14px;
    line-height: 22px;
    color: #000;
    padding-left: 4px;
}
.header__switcher {
    cursor: pointer;
    margin-left: 25px;
}
.header__switcher_btn {
    margin: 0 3px;
    width: 34px;
    height: 16px;
    background: #f8f8f8;
    border: 1px solid #e0e0e0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    border-radius: 32px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    position: relative;
}
.header__switcher_btn-toggler {
    position: absolute;
    left: 0;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background: #fff;
    border: 0.5px solid rgba(0, 0, 0, 0.04);
    -webkit-box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.15), 0px 1px 1px rgba(0, 0, 0, 0.08);
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.15), 0px 1px 1px rgba(0, 0, 0, 0.08);
    -webkit-transition: left 0.2s ease-in-out;
    transition: left 0.2s ease-in-out;
}
.header__switcher_active .header__switcher_btn-toggler {
    left: 18px;
    -webkit-transition: left 0.2s ease-in-out;
    transition: left 0.2s ease-in-out;
}
